.hero {
  padding-top: 60px;
  &__title {
    @include fontSize(28px, 1.2);
    max-width: 502px;
    padding-bottom: 28px;
  }
  &__text {
    @include fontSize(14px, 1.5);
    max-width: 502px;
    padding-bottom: 28px;
  }
  &__btn {
    @include fontSize(13px, 1.5);
    width: 98px;
    height: 32px;
    border-radius: 30px;
    background-color: $primary-green-accent;
    border: none;
  }
  & .container {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
  }
}

.slider {
  @media screen and (max-width: 767px) {
    display: none;
  }
}

@media screen and (min-width: 768px) and (max-width: 1279px) {
  .hero {
    & .container {
      position: relative;
      height: 409px;
    }

    &__title {
      @include fontSize(42px, 1.2);
      max-width: 537px;
      padding-bottom: 44px;
    }
    &__text {
      width: 336px;
      padding-bottom: 40px;
    }

    .slider {
      // margin-top: auto;
      position: absolute;
      right: 0;
      bottom: 0;
    }
  }
}

@include desktop {
  .hero {

    padding-top: 14px;
    &__title {
      @include fontSize(48px, 1.2);
    }
    &__text {
      padding-bottom: 40px;
    }
  }
}
